import React from 'react';

class ArjunaneFlutterHelper extends React.Component {

    constructor(props) {
        super(props);
        var date = new Date();
        var dateNow = `${date.getFullYear()}-${date.getMonth()}-${date.getDate()} ${date.getHours()}:${date.getMinutes()}:${date.getMilliseconds()}`
        this.syntax = [
`Color redHex = Helper.fromHex("#e74c3c");

Container(
    color: redHex
);`,

`var date = Helper.dateNow();

print("date: $date");
// date: ${dateNow}` 
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "helper" ? "block" : "none"}}>
                    <h1></h1>
                    <div className="p-x">
                        <div className="col-mx">
                            The <strong>FlashData</strong> is a local data that will only be available for the next request, and is then automatically cleared.
                        </div>
                        <div className="col-mx">
                            <h3>fromHex</h3>
                            <pre className="syntax">{this.syntax[0]}</pre>

                            <h3>constructor</h3>
                            <pre className="syntax">{this.syntax[1]}</pre>
                            <h4>properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>key</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        Link address
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>data</strong> &rarr; <span className="fc-b">dynamic</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        File Name
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterHelper;