import React from 'react';
import {Route, Switch} from 'react-router-dom';
import helper from '../../../helper/helper';
import ArjunaneLeft from './../container_left';
import ArjunaneDatePickerV1Home from './docs/adp_1_home';

class ArjunaneDatePickerV1Container extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            uri : helper.uriHashSegment(2)
        };
    }

    componentDidMount() {
        helper.appendScript(process.env.PUBLIC_URL + "/js/documentations.js");
        helper.appendScript(process.env.PUBLIC_URL + "/js/syntax/arjunane-syntax-highlight.js", () => {
            window.arSyntax.init(".syntax", 'javascript');
        });

        window.onpopstate = () => {
            this.setState({uri: helper.uriHashSegment(2)});
        }
    }

    navigationsClick = (page) => {
        this.setState({uri: helper.uriHashSegment(2)});
    }

    render() {
        return  <React.Fragment>
                    <ArjunaneLeft uri={this.state.uri} category="arjunane-date-picker-v1" navClick={this.navigationsClick}/>
                    
                    <ArjunaneDatePickerV1Home uri={this.state.uri}/>

                </React.Fragment>;
    }
}

export default ArjunaneDatePickerV1Container;