import React from 'react';
import { withRouter } from 'react-router-dom';
import helper from '../../helper/helper';
import modelDocumentation from '../../model/ModelDocumentation';

class ArjunaneLeft extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            activeClass : this.props.uri
        }
        this.unlisten = this.props.history.listen((location, action) => {
            this.setState({activeClass: helper.uriHashSegment(2)});
        });
    }

    componentWillUnmount() {
        this.unlisten();
    }

    changePage(e, page) {
        e.preventDefault();
        var url = e.target.getAttribute("href");
        this.props.history.push({
            pathname: helper.getUrlPath(url)
        });
        this.setState({activeClass: page});
        
        this.props.navClick(page);
    }

    setNavigations() {
        this.html = new Array();
        modelDocumentation.getNavigations()[this.props.category].forEach( (val) => {
            if(val.children) {
                this.html.push(<React.Fragment>
                    <strong>{val.title}</strong>
                    <div style={{paddingLeft: 10 + "px"}}>
                        {val.children.map(v => {
                            return <a className={this.state.activeClass === v.url || (v.url === "" && this.state.activeClass === null) ? "active" : ""} onClick={(e) => this.changePage(e, v.url)} href={helper.setUrl("documentation/" + this.props.category + "/" + v.url)}>{v.title}</a>
                        })}
                    </div>
                    
                </React.Fragment>);
                return;
            }
            
            this.html.push(<a className={this.state.activeClass === val.url || (val.url === "" && this.state.activeClass === null) ? "active" : ""} onClick={(e) => this.changePage(e, val.url)} href={helper.setUrl("documentation/" + this.props.category + "/" + val.url)}>{val.title}</a>)
        });
        return this.html;
    }

    render() {
        return <nav>
                    <div class="nav-container">
                        {this.setNavigations()}
                    </div>
                </nav>;
    }
}

export default withRouter(ArjunaneLeft);