import React from 'react';

class ArjunaneFlutterForms extends React.Component {

    constructor(props) {
        super(props);
        var date = new Date();
        var dateNow = `${date.getFullYear()}-${date.getMonth()}-${date.getDate()} ${date.getHours()}:${date.getMinutes()}:${date.getMilliseconds()}`
        this.syntax = [
`
class ExampleController extends Controller<ExamplePage> {

    Forms forms = new Forms();
    Validation validation = new Validation();

    void validateFormLogin() {
        if(form.validate()) {
          showAlertDialog(context: context, message: valid.all().toString());
        }
    }
    @override
    Widget build(BuildContext context) {

        // TODO: implement build
        return ExampleView(this);
    }
}`,

`class ExampleView extends View<ExampleController> {

    ExampleView (ExampleController prop) : super(prop);
  
    @override
    Widget build(BuildContext context) {
      return Scaffold(
        appBar: AppBar(
          title: Text("Your Title")
        ),
        body: Container(
          child: prop.forms.formOpen(
            child: Column(
              children: [

                prop.forms.formInput('E-Mail', "email", 
                  placeholder: "Input your E-Mail ...", 
                  validator: (val) => prop.validation.setRules(val, "E-Mail", 'required|email', key: 'email'),
                  icon: Icons.email),
                  
                prop.forms.formInput('Password', "password", 
                  placeholder: "Input your Password ...", 
                  obscureText: true, 
                  validator: (val) => prop.validation.setRules(val, "Password", 'required|min_length:6|max_length:50', key: 'password'),
                  icon: Icons.vpn_key),

                button('SUBMIT', onPressed: prop.validateFormLogin )
              ],
            )
          )
        ),
     );
  
    }
  
}` 
        ];
    }

    render() {
        return <div className="container nav-left" style={{display: this.props.uri === "forms" ? "block" : "none"}}>
                    <h1>Forms class</h1>
                    <div className="p-x">
                        <div className="col-mx">
                            The <strong>Form Helper</strong> file contains functions that assist in working with forms.
                        </div>
                        <div className="col-mx">
                            <h3>Example</h3>
                            <div className="col-mx">
                                ExampleController.dart
                            </div>
                            <pre className="syntax">{this.syntax[0]}</pre>

                            <div className="col-mx">
                                ExampleView.dart
                            </div>
                            <pre className="syntax">{this.syntax[1]}</pre>
                            <h4>properties</h4>
                            <ul>
                                <li>
                                    <div className="col-mx">
                                        <strong>key</strong> &rarr; <span className="fc-b">String</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        Link address
                                    </div>
                                </li>
                                <li>
                                    <div className="col-mx">
                                        <strong>data</strong> &rarr; <span className="fc-b">dynamic</span> <strong className="fc-r">@required</strong>
                                    </div>
                                    <div className="col-mx">
                                        File Name
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>;
    }
}

export default ArjunaneFlutterForms;